.review-section {
  margin: 30px auto 50px;
  padding-top: 62px;
  padding-bottom: 66px;
  width: 926px;
  //   height: 350px;
}
// .customer-reviews {
//   display: flex;
// }
.slick-slide {
  height: 328px;
  margin-bottom: 36px;
}

.customer-reviews__img {
  margin-left: auto;
  margin-right: auto;
}

.customer-reviews__quotation {
  position: relative;
  width: 926px;
  height: 135px;
  margin-top: 15px;
  background-color: rgb(250, 250, 250);
}

.reviews__text-line {
  position: absolute;
  margin-left: 50px;
  margin-right: auto;
  width: 750px;
  height: 60px;
  left: 50px;
  top: 32px;
  //   height: 100%;
  background-image: repeating-linear-gradient(
    rgba(100%, 100%, 100%, 0%) 0px,
    rgba(100%, 100%, 100%, 0%) 29px,
    #e1e1e1 29px,
    #e1e1e1 30px
  );
  @media screen and (min-width: 768px) {
    background-image: repeating-linear-gradient(
      rgba(100%, 100%, 100%, 0%) 0px,
      rgba(100%, 100%, 100%, 0%) 28px,
      #e1e1e1 28px,
      #e1e1e1 29px
    );
  }
}
.customer-reviews__text {
  font-family: var(--main-font);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.86;
  letter-spacing: 0.04em;
  text-align: center;
}

.customer-reviews__svg {
  position: absolute;
  margin-left: 60px;
  margin-top: 14px;
}

