.customer-reviews__drops {
  margin-left: 448px;
  margin-right: auto;
  margin-bottom: 15px;
  margin-top: 15px;
}

.customer-reviews__city {
  text-align: center;
  font-family: var(--main-font);
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.85px;
  letter-spacing: 0em;
}

.customer-reviews__buttons {
  margin-top: 35px;
  margin-left: 426px;
  margin-right: auto;
}

.customer-reviews__house-svg:not(:last-child) {
  margin-right: 18px;
}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slick-dots li {
  margin: 0px 10px;
}
.slick-dots button {
  font-size: 0;
  width: 10px;
  height: 16px;
  background-color: rgb(217, 217, 217);
  border-radius: 50%;
  border-color: rgb(217, 217, 217);
}

.slick-dots li.slick-active button {
  background: url('/images/customer_reviews/home.svg');
  border-color: rgb(250, 250, 250);
  width: 16px;
  height: 16px;
}
// .slick-track {
//   display: flex;
// }
.slick-list {
  overflow: hidden;
}
